/* colors */

$white: #fff;
$black: #4D4D4D;
$red: #df2f4a;
$icon-red: #f74236;
$green: #1C6E5C;
$grey: #a4acb8;
$grey-dark: #404250;
$grey-light: rgba(28,110,92,.05);
$blue: blue;
$yellow: #FDD81D;

$main-color: $black;
$link-color: $main-color;

/* main variables */

$min-width: 320px;
$max-width: 960px;
$base-bg: $white;
$base-font-size: 1.6rem;
$mobile-font-size: 1.4rem;
$base-line-height: 2.7rem;
$base-font-family: 'Work Sans', sans-serif;
$custom-font-family: 'avalon', sans-serif;

//text, titles
$text-color: $black;
$title-color: $black;
$title-font-weight: 600;

$font-size-h1: 5rem;
$line-height-h1: 6rem;

$font-size-h2: 3.6rem;
$line-height-h2: 4.4rem;

$font-size-h3: 2.4rem;
$line-height-h3: 2.9rem;

$font-size-h4: 1.6rem;
$line-height-h4: 6rem;


$headerHeight: 20rem;
$headerHeightMobile: 7.2rem;
$footerHeight: 69rem;

$default-transition: all .2s linear;





/* Grid */
$vertical-gutter: 2rem;
$horizontal-gutter: 2rem;

						//.col-2-of-3
@function grid($col, $numOfCol){
	$numOfGutters: $numOfCol - 1; //number of gutters between columns in a row
	$addGutter: ($col - 1) * $horizontal-gutter; //number of gutters, width of which should be added to column
	
	$width: calc( 
		(((100% - #{$horizontal-gutter} * #{$numOfGutters}) / #{$numOfCol}) * #{$col}) + #{$addGutter}							
	);

	@return $width;
}